/*=============== BASE ===============*/
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: #fff;
  background-color: #fff;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}


/*=============== HOME ===============*/
.header{
width: 100%;
height: 600px;
background: linear-gradient(0deg, rgba(0, 0, 0, 0.10) 0%, rgba(0, 0, 0, 0.01) 100%), url(./images/Poster.png), transparent 50% / cover no-repeat;
}

.nav{
display: flex;
justify-content: space-evenly;
align-items: center;
width: 100%;
height: 80px;
}

.header-logo{
display: inline-flex;
align-items: center;
gap: 24px;
font-weight: bold;
letter-spacing: 1px;
}

.logo{
width: 50px;
height: 50px;
background: url(./images/tv.png), transparent 50% / cover no-repeat;
}

.header-logo span{
font-family: 'DM Sans', sans-serif;
font-size: 24px;
font-weight: 700;
}

.search-bar{
display: flex;
width: 525px;
padding: 6px 10px;
justify-content: space-between;
align-items: center;
border-radius: 6px;
border: 2px solid var(--gray-300, #D1D5DB);
}

.search-input{
background-color: transparent;
color: #fff;
border: none;
outline: none;
width: 90%;
}

.search-input::placeholder{
color: #fff;
font-family: 'DM Sans', sans-serif;
font-size: 16px;
font-weight: 400;
line-height: 24px;
}

.menu-box{
display: flex;
column-gap: 2rem;
}

.sign-in{
cursor: pointer;
font-family: 'DM Sans';
font-size: 16px;
font-weight: 700;
}

.menu{
background: #BE123C;
border-radius: 50%;
width: 32px;
height: 32px;
text-align: center;
cursor: pointer;
}

.menu svg{
width: 24px;
height: 24px;
padding-top: 5px;
}

.hero{
display: flex;
justify-content: space-between;
align-items: center;
margin: 100px  20px 100px 135px;
}

.description-box{
display: inline-flex;
flex-direction: column;
align-items: flex-start;
gap: 16px;
}

.hero-movie-title{
width: 404px;
font-family: 'DM Sans';
font-size: 48px;
font-style: normal;
font-weight: 700;
line-height: 56px;
}

.rating{
width: 184px;
height: 17px;
display: flex;
column-gap: 10px;
}

.imdb{
display: inline-flex;
align-items: center;
gap: 10px;
}

.imdb-img{
background: url(./images/IMDB.png), lightgray 50% / cover no-repeat;
width: 35px;
height: 17px;
}

.imdb span{
color: #FFF;
font-family: 'DM Sans';
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: 12px;
}

.rotten-tomatoes{
display: inline-flex;
align-items: center;
gap: 10px;
}

.rotten-tomatoes-img{
background: url('./images/PngItem_1381056\ 1.png'), transparent 50% / cover no-repeat;
width: 16px;
height: 17px;
}

.rotten-tomatoes span{
font-family: 'DM Sans';
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: 12px;
}

.description{
width: 302px;
font-family: 'DM Sans';
font-size: 14px;
font-style: normal;
font-weight: 500;
line-height: 18px;
}

.hero-button{
display: flex;
padding: 6px 16px;
align-items: center;
gap: 8px;
border-radius: 6px;
background:  #BE123C;
}

.hero-button svg{
width: 20px;
height: 20px;
}

.hero-button span{
color: var(--white, #FFF);
font-family: 'DM Sans';
font-size: 14px;
font-style: normal;
font-weight: 700;
line-height: 24px;
text-transform: uppercase;
}

.pagination-box{
width: 36px;
height: 110px;
}

.pagination{
display: inline-flex;
flex-direction: column;
align-items: center;
gap: 10px;
}

.page-grey{
color: var(--gray-400, #9CA3AF);
font-family: 'DM Sans';
font-size: 12px;
font-style: normal;
font-weight: 700;
line-height: 14px;
}

.page-active{
color: #FFF;
font-family: 'DM Sans';
font-size: 16px;
font-weight: 700;
line-height: 14px;
}

.rectangle{
position: absolute;
top: 50%;
width: 20px;
height: 3px;
margin-right: 36px;
border-radius: 6px;
background: var(--white, #FFF);
}

/*--------------------------FEATURED--------------------------------------*/

.featured{
max-width: 100%;
margin: 50px 50px 0px 100px ;
}

.featured-title{
display: flex;
justify-content: space-between;
align-items: center;
}

.featured-title span{
color: #000;
font-family: 'DM Sans';
font-size: 36px;
font-style: normal;
font-weight: 700;
line-height: normal;
}

.see-more{
display: flex;
align-items: center;
gap: 8px;
}

.see-more span{
color: var(--rose-700, #BE123C);
font-family: 'DM Sans';
font-size: 18px;
font-style: normal;
font-weight: 400;
line-height: 24px;
}

.see-more svg{
width: 20px;
height: 20px;
}

.movie-list{
position: relative;
padding-top: 50px;
display: grid;
grid-template-columns: repeat(4, 1fr);
gap: 50px;
}

.movie-card{
display: flex;
flex-direction: column;
align-items: flex-start;
gap: 12px;
}

.poster{
width: 250px;
height: 370px;
}

.poster-img{
position: relative;
width: 250px;
height: 370px;
}

.poster-rating{
position: absolute;
display: flex;
justify-content: center;
gap: 114px;
padding-top: 10px;
z-index: 100;
}

.tv-series{
display: flex;
padding: 3px 8px;
align-items: flex-start;
gap: 10px;
border-radius: 12px;
background: rgba(243, 244, 246, 0.50);
backdrop-filter: blur(1px);
margin: auto 10px;
}

.tv-series span{
color: var(--gray-900, #111827);
font-family: 'DM Sans';
font-size: 12px;
font-style: normal;
font-weight: 700;
line-height: normal;
}

.favorite{
width: 30px;
height: 29.211px;
flex-shrink: 0;
background-color: rgba(243, 244, 246, 0.50);
backdrop-filter: blur(1px);
border-radius: 50%;
text-align: center;
}

.heart svg{
width: 20px;
height: 19.474px;
margin-top: 15%;
cursor: pointer;
}

.movie-detail{
color: var(--gray-400, #9CA3AF);
font-family: DM Sans;
font-size: 12px;
font-style: normal;
font-weight: 700;
line-height: normal;
}

.movie-title{
color: var(--gray-900, #111827);
font-family: DM Sans;
font-size: 18px;
font-style: normal;
font-weight: 700;
line-height: normal;
}

.movie-rating{
display: flex;
width: 250px;
justify-content: space-between;
align-items: flex-start;
}

.movie-rating span{
color: var(--gray-900, #111827);
font-family: DM Sans;
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: 12px; /* 100% */
}

.movie-category{
color: var(--gray-400, #9CA3AF);
font-family: DM Sans;
font-size: 12px;
font-style: normal;
font-weight: 700;
line-height: normal;
}


/*=========================FOOTER====================*/
.footer{
display: flex;
flex-direction: column;
align-items: center;
gap: 36px;
margin-top: 80px;
}

.socials{
display: flex;
align-items: center;
gap: 48px;
color: #000;
}

.fa{
width: 24px;
height: 27.429px;
color: #000;
}

.links{
display: flex;
align-items: flex-start;
gap: 48px;
}

.links span{
color: var(--gray-900, #111827);
font-family: 'DM Sans';
font-size: 18px;
font-weight: 700;
}

.copyright{
color: var(--gray-500, #6B7280);
font-family: 'DM Sans';
font-size: 18px;
font-weight: 700;
margin-bottom: 20px;
}

/* CSS for Error Message */
.loading {
font-size: 30px;
color: #666666;
font-weight: 600;
width: auto;
height: auto;
text-align: center;
position: absolute;
top: 50%;
left: 50%;
margin: -50px 0 0 -200px;
}

.search-results{
color: #000;
}

/* Add a media query for screens smaller than 768px */
@media (max-width: 768px) {
.header {
  height: 400px; 
}

.search-bar {
  width: 90%; 
}

.description-box {
  width: 100%; 
}

.pagination-box {
  display: none; 
}

.featured {
  margin-left: 20px; 
}

.movie-list {
  grid-template-columns: repeat(2, 1fr); 
}
}

/* Add a media query for screens smaller than 480px */
@media (max-width: 480px) {
.header {
  height: 300px; 
}

.search-input {
  font-size: 14px; 
}

.description-box {
  gap: 10px; 
}

.featured-title {
  flex-direction: column; 
}

.movie-list {
  grid-template-columns: 1fr;
}

.poster {
  width: 100%;
  height: auto;
}
}
